import axios from './api/axios';

export const GCP = {
  async getPassportData(params) {
    /*
			query_params:
				pinfl
				passport_data
		*/
    return await axios.get('api/info/get_passport_data/', {params});
  },
  async getBirthPassportData(params) {
    /*
			query_params:
				doc_seria=III-TN
        doc_number=0017268
		*/
    return await axios.get('api/info/get_birth_passport_data/', {params});
  },
  async getAddressInfo(params) {
    return await axios.get('api/info/get_address/', {params});
  },
  async getIsConvicted(params) {
    return await axios.get('api/info/get_is_convicted/', {params});
  },
  // async get
};
