String.prototype.isUpperCase = function () {
  if (this == this.toUpperCase()) {
    return true;
  } else return false;
};

export const useCyrillicToLatin = async (text = '') => {
  if (text.isUpperCase()) {
    Object.entries(translit).forEach(([key, value]) => {
      text = text.replace(new RegExp(key, 'g'), value);
    });
    return text.toUpperCase();
  } else {
    Object.entries(translit).forEach(([key, value]) => {
      text = text.replace(new RegExp(key, 'g'), value);
    });
    return text;
  }
};

(async () => {
  const res = await useCyrillicToLatin('Суд-экспертлик фаолиятини ривожлантиришни қўллаб-қувватлаш жамғармаси фаолиятини бошқариш кенгашининг дастлабки йиғилиши бўлиб ўтди');
  console.log(res);
})();
